<template>
    <div class="home">
        <div class="djtIndex-top">
            <div class="djtIndex-top-mean">
                <div class="djtIndex-top-mean-left">
                    <img src="../../assets/img/logo.png" alt="" class="djtIndex-top-mean-left-img">
                </div>
                <div class="djtIndex-top-mean-right">
                    <template v-if="!address">
                        <span class="djtIndex-top-mean-right-text" @click="showMask()">{{ short_address }}</span>
                    </template>
                    <template v-if="address">
                        <span class="djtIndex-top-mean-right-text">{{ short_address }}</span>
                    </template>

                    <img src="../../assets/img/lang.png" alt="" class="djtIndex-top-mean-right-img1" @click="showLang()">
                    <img src="../../assets/img/mean.png" alt="" class="djtIndex-top-mean-right-img2" @click="showMean()">
                </div>
            </div>
        </div>
        <div class="djtIndex-content">
            <div class="djtnew-top">
                <div class="djtnew-top-title">奖池</div>
                <div class="djtnew-top-num">{{ info.prize }} DJT</div>
                <div class="djtnew-top-tab" v-if="!lotteryShow">
                    <div class="djtnew-top-tab-time">{{ kaiInfo.hour }}</div>
                    <span class="djtnew-top-tab-dian">:</span>
                    <div  class="djtnew-top-tab-time">{{ kaiInfo.minute }}</div>
                    <span class="djtnew-top-tab-dian">:</span>
                    <div  class="djtnew-top-tab-time">{{ kaiInfo.second }}</div>
                </div>
                <div v-if="lotteryShow" class="djtnew-lottery">
                    <div class="djtnew-lottery-result">等待开奖</div>
                    <div class="djtnew-lottery-desc">
                        大奖得主：{{ luck.address }} <br/>
                        获奖队伍：
                        <template v-if="luck.luck_team_type=='A'">
                            特朗普队
                        </template>
                        <template v-if="luck.luck_team_type=='B'">
                            马斯克
                        </template>
                        <template v-if="luck.luck_team_type=='C'">
                            自由女神
                        </template>
                        <template v-if="luck.luck_team_type=='D'">
                            拜登
                        </template>

                    </div>
                    <template v-if="kaiShow">
                        <div class="djtnew-lottery-button" @click="lotterySubmit($event)">开奖</div>
                    </template>
                </div>
            </div>

            <div class="djtnew-content">
                <div class="djtnew-content-title">购买KEY</div>
                <div class="djtnew-content-tab">
                    <div>参与数量</div>
                    <div>KEY价格：{{ info.price }}DJT</div>
                </div>
                <div>
                    <input type="number" v-model="keyNum" class="djtnew-content-input" @keyup="checkNum()">
                </div>
                <div class="djtnew-content-item">
                    <div :class="activeKey==1?'djtnew-content-item-active-button':'djtnew-content-item-button'" @click="chooseNum(1)">1</div>
                    <div :class="activeKey==100?'djtnew-content-item-active-button':'djtnew-content-item-button'" @click="chooseNum(100)">100</div>
                    <div :class="activeKey==1000?'djtnew-content-item-active-button':'djtnew-content-item-button'" @click="chooseNum(1000)">1000</div>
                    <div :class="activeKey==10000?'djtnew-content-item-active-button':'djtnew-content-item-button'" @click="chooseNum(10000)">10000</div>
                </div>
                <div class="djtnew-content-item-title">
                    选择战队
                </div>
                <div class="djtnew-content-item">
                    <div :class="activeType==0?'djtnew-content-item-active-button':'djtnew-content-item-button'"  @click="chooseType(0)">特朗普</div>
                    <div :class="activeType==1?'djtnew-content-item-active-button':'djtnew-content-item-button'"  @click="chooseType(1)">马斯克</div>
                    <div :class="activeType==2?'djtnew-content-item-active-button':'djtnew-content-item-button'" @click="chooseType(2)">自由女神</div>
                    <div :class="activeType==3?'djtnew-content-item-active-button':'djtnew-content-item-button'" @click="chooseType(3)">拜登</div>
                </div>
                <div class="djtnew-content-button" @click="playGame($event)">立即参与</div>

                <div class="djtnew-msg">
                    <div><img src="../../assets/img/tab1.png" alt="" class="djtnew-msg-img"></div>
                    <div class="djtnew-msg-right">
                        <div class="djtnew-msg-right-title">特朗普</div>
                        <div class="djtnew-msg-right-desc">
                            1.10%推荐收益， <br/>
                            2.34%给战队分红， <br/>
                            3.56%进入奖池等待开大奖
                        </div>
                    </div>
                </div>
                <div class="djtnew-msg">
                    <div class="djtnew-msg-right">
                        <div class="djtnew-msg-right-title">马斯克</div>
                        <div class="djtnew-msg-right-desc">
                            1.10%推荐收益， <br/>
                            2.42%给战队分红， <br/>
                            3.48%进入奖池等待开大奖
                        </div>
                    </div>
                    <div><img src="../../assets/img/tab2.png" alt="" class="djtnew-msg-img"></div>
                </div>
                <div class="djtnew-msg">
                    <div><img src="../../assets/img/tab3.png" alt="" class="djtnew-msg-img"></div>
                    <div class="djtnew-msg-right">
                        <div class="djtnew-msg-right-title">自由女神</div>
                        <div class="djtnew-msg-right-desc">
                            1.10%推荐收益， <br/>
                            2.42%给战队分红， <br/>
                            3.48%进入奖池等待开大奖
                        </div>
                    </div>
                </div>
                <div class="djtnew-msg">
                    <div class="djtnew-msg-right">
                        <div class="djtnew-msg-right-title">拜登</div>
                        <div class="djtnew-msg-right-desc">
                            1.10%推荐收益， <br/>
                            2.24%给战队分红， <br/>
                            3.66%进入奖池等待开大奖
                        </div>
                    </div>
                    <div><img src="../../assets/img/tab4.png" alt="" class="djtnew-msg-img"></div>
                </div>
                <div class="djtnew-bottom">
                    - 友情链接 -
                </div>
                <div class="djtnew-bottom-tab">
                    <div>
                        <div>
                            <img src="../../assets/img/bottom1.png" alt="" class="djtnew-bottom-tab-img">
                        </div>
                        <div class="djtnew-bottom-tab-text">DJT DAPP</div>
                    </div>
                    <div>
                        <div>
                            <img src="../../assets/img/bottom2.png" alt="" class="djtnew-bottom-tab-img">
                        </div>
                        <div class="djtnew-bottom-tab-text">电报</div>
                    </div>
                    <div>
                        <div>
                            <img src="../../assets/img/bottom3.png" alt="" class="djtnew-bottom-tab-img">
                        </div>
                        <div class="djtnew-bottom-tab-text">推特</div>
                    </div>
                </div>
            </div>

        </div>
        <div v-if="meanShow" class="index_pop_content">
            <div class="index_pop_top">
                <div>
                    <img src="../../assets/img/logo.png" alt="" class="index_pop_top-left">
                </div>
                <div @click="closeMean">
                    <img src="../../assets/img/mean-close.png" alt="" class="index_pop_top-right">
                </div>
            </div>
            <div class="index_pop_main_tab">
                <div class="index_pop_middle"  @click="toIndex()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-home.png" alt="">
                        <span>首页</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toOrder()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-order.png" alt="">
                        <span>我的KEY</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toTrade()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-trade.png" alt="">
                        <span>交易</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toInvite">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-invite.png" alt="">
                        <span>我的邀请</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toWhite">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-book.png" alt="">
                        <span>游戏白皮书</span>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="maskShow" class="index_mask">
            <div class="index_mask-content" @click="checkNet()">
                <div class="index_mask-title">{{ lang_list.index_mask }}</div>
                <img src="../../assets/img/mask.png" alt="" class="index_mask-img">
            </div>
            <div @click="closeMask()">
                <img src="../../assets/img/close.png" alt="" class="index_mask-close">
            </div>
        </div>
        <div v-if="langShow" class="index_lang">
            <div :class="lang=='zh'?'index_lang-button-active':'index_lang-button'" @click="switchLang('zh')">中文</div>
            <div :class="lang=='en'?'index_lang-button-active':'index_lang-button'" @click="switchLang('en')">EN</div>
        </div>
    </div>
</template>
<script>
    import {WOW} from 'wowjs';
    let provider, signer, defaultAccount, networkVersion;
    let conContract,djtContract;
    export default {
        data() {
            return {
                lang:'zh',
                lang_list:{},
                langShow:false,
                address: '',
                short_address: 'connect',
                p_address:'',
                meanShow: false,
                maskShow:false,
                lotteryShow:false,
                banner:0,
                luck:{
                    address:'',
                    luck_team_type:0,
                },
                info:{
                    rate:0,
                    start_time:'',
                    end_time:'',
                    price:'',
                    total:'',
                    prize:'',
                    parent:'',
                    deposit:'',
                    invit_rewards:'',
                    team_rewards:'',
                    luck_rewards:'',
                },
                kaiInfo:{
                    hour:0,
                    minute:0,
                    second:0,
                },
                keyNum:'',
                activeKey:1,
                activeType:0,
                djtNum:0,
                lpdisable:false,
                fudisable:false,
                kaiShow:false,
            }
        },
        created: function () {

        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang')?sessionStorage.getItem('lang'):'en';
            this.p_address = this.$route.query.p_address?this.$route.query.p_address:this.getUrlParam('p_address');
            this.lang_list = this.en;
            switch(this.lang){
                case 'zh':
                    this.lang_list = this.zh;
                    break;
                case 'en':
                    this.lang_list = this.en;
                    break;
            }
            this.checkNet();
            var wow = new WOW(
                {
                    boxClass: 'wow',      // animated element css class (default is wow)
                    animateClass: 'animated', // animation css class (default is animated)
                    offset: 0,          // distance to the element when triggering the animation (default is 0)
                    mobile: true,       // trigger animations on mobile devices (default is true)
                    live: true,       // act on asynchronously loaded content (default is true)
                    callback: function (box) {
                        // the callback is fired every time an animation is started
                        // the argument that is passed in is the DOM node being animated
                    },
                    scrollContainer: null // optional scroll container selector, otherwise use window
                }
            );
            wow.init();
        },
        methods: {
            //开奖
            async lotterySubmit(event){
                let _this = this;
                if(!this.lotteryShow){
                    this.$toast('暂未开奖');
                    return false;
                }
                if(_this.fudisable){
                    return false;
                }
                _this.fudisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try{
                    const gasPrice = await provider.getGasPrice();
                    let res = await conContract.nextGame({
                        gasPrice: gasPrice,
                        gasLimit:800000
                    });
                    if(res){
                        _this.fudisable = false;
                        _this.$toast('操作成功');
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }catch (e) {
                    _this.$toast('操作失败');
                    _this.fudisable = false;
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            // 参与
            async playGame(event){
                let _this = this;
                const gasPrice = await provider.getGasPrice();
                if(this.keyNum<=0){
                    this.$toast('请输入购买KEY数量');
                    return false;
                }
                if(this.djtNum > this.banner){
                    this.$toast('DJT余额不足');
                    return false;
                }
                if(this.lpdisable){
                    return false;
                }
                this.lpdisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                await djtContract.allowance(defaultAccount,_this.api.conAddress).then(async res =>{
                    let parentAddress =_this.p_address;
                    if(!parentAddress){
                        parentAddress = '0x0000000000000000000000000000000000000000'
                    }
                    try{
                        if(parseFloat(ethers.utils.formatEther(res))>0){
                            let res = await conContract.play(_this.activeType,_this.keyNum,parentAddress,{
                                gasPrice: gasPrice,
                                gasLimit:400000
                            })
                            if(res){
                                _this.lpdisable = true;
                                _this.$toast('操作成功');
                                setTimeout(function () {
                                    location.reload();
                                },3000)
                            }
                        }
                        else{
                            let tx = await djtContract.approve(_this.api.conAddress,'0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff',{
                                gasPrice: gasPrice,
                                gasLimit:250000
                            })

                            if(tx){
                                let res = await conContract.play(_this.activeType,_this.keyNum,parentAddress,{
                                    gasPrice: gasPrice,
                                    gasLimit:400000
                                })
                                if(res){
                                    _this.lpdisable = true;
                                    _this.$toast('操作成功');
                                    setTimeout(function () {
                                        location.reload();
                                    },3000)
                                }
                            }
                        }
                    }catch (e) {
                        _this.lpdisable = true;
                        _this.$toast('操作失败');
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }

                }).catch(err => {
                    _this.lpdisable = true;
                    _this.$toast('操作失败');
                    return false;
                });
            },
            checkNum(){
                this.keyNum = parseInt(this.keyNum)
                this.djtNum = parseInt(this.keyNum)*parseFloat(this.info.price)
            },
            chooseType(type){
                let _this = this;
                _this.activeType = type;

            },
            chooseNum(num){
                let _this = this;
                _this.activeKey = num;
                _this.keyNum = num;
                _this.djtNum = parseInt(_this.keyNum)*parseFloat(_this.info.price)

            },
            todian(){
                window.open('https://t.me/DJT_Group','_self');
            },
            toTwitte(){
                window.open('https://twitter.com/DJTsystem','_self');
            },
            toTrade(){
                window.open('https://pancakeswap.finance/swap?outputCurrency=0x41D8675E6C64939c3245908EEaf827A995Fc47Ff&inputCurrency=0x55d398326f99059ff775485246999027b3197955&utm_source=tokenpocket','_self');
            },
            toWhite(){
              window.open('/static/djt.pdf');
            },
            toIndex(){
                this.meanShow = false;
            },
            toOrder(){
                this.$router.push({
                    name: 'myKey',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toInvite(){
                this.$router.push({
                    name: 'myInvite',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            switchLang(type){
                switch(type){
                    case 'zh':
                        sessionStorage.setItem('lang','zh');
                        break;
                    case 'en':
                        sessionStorage.setItem('lang','en');
                        break;
                }
                location.reload();
            },
            //显示菜单
            showLang(){
                if(this.langShow){
                    this.langShow = false;
                }else{
                    this.langShow = true;
                }

            },
            //显示菜单
            showMask(){
                this.maskShow = true;
            },
            //关闭菜单
            closeMask(){
                this.maskShow = false;
            },
            //显示菜单
            showMean(){
                this.meanShow = true;
            },
            //关闭菜单
            closeMean(){
                this.meanShow = false;
            },
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;

                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
                    _this.$toast(_this.lang_list.index_warn);
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                window.location.reload();
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(async function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        }
                        else if (networkVersion == 97) { //币安测试网
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        conContract = new ethers.Contract(_this.api.conAddress,conAbi, signer);
                        djtContract =  new ethers.Contract(_this.api.djtAddress,djtAbi, signer);
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                            _this.getBanner();
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
            async getBanner(){  //获取余额
                let _this = this;
                await djtContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.banner =  Math.floor(parseFloat(ethers.utils.formatEther(res))* 100000000000)/100;
                    console.log(_this.banner)
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.current_round().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.info.rate = res;
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.gameInfo().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.info.start_time = ethers.utils.formatEther(res[0])*1000000000000000000;
                    _this.info.end_time = ethers.utils.formatEther(res[1])*1000000000000000000;
                    _this.info.price = ethers.utils.formatEther(res[2])*1000000000;
                    _this.info.total =parseInt(ethers.utils.formatEther(res[3])*1000000000);
                    _this.info.prize =parseFloat(ethers.utils.formatEther(res[4])*1000000000);
                    let timestamp =parseInt(Date.parse(new Date())/1000);
                    let shenTime = parseInt(_this.info.end_time)-parseInt(timestamp);
                    let yuTime =setInterval(function () {
                        _this.formatSeconds(shenTime);
                        shenTime--;
                    },1000);

                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.luck_address().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.luck.address = res;
                    if(_this.luck.address!='0x0000000000000000000000000000000000000000'){
                        if(res.toLowerCase()==_this.address.toLowerCase()){
                            _this.kaiShow = true;
                        }

                         conContract.luck_team_type().then(res1 => {
                            // let etherString = ethers.utils.formatEther(res);
                            //  console.log(res1)
                             _this.luck.luck_team_type = res1 ;
                        }).catch(err => {
                            console.log(err);
                            return;
                        });
                    }
                    console.log(res)
                }).catch(err => {
                    console.log(err);
                    return;
                });

                await conContract.myInfo().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.info.parent = res.parent;
                    _this.info.deposit = res.deposit;
                    _this.info.invit_rewards = res.invit_rewards;
                    _this.info.team_rewards = res.team_rewards;
                    _this.info.luck_rewards = res.luck_rewards;
                    console.log(_this.info)
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            formatSeconds(value) {
                var theTime = parseInt(value);// 需要转换的时间秒
                var theTime1 = 0;// 分
                var theTime2 = 0;// 小时
                if(theTime<=0){
                    this.kaiInfo.hour = '00';
                    this.kaiInfo.minute = '00';
                    this.kaiInfo.second = '00';
                    this.lotteryShow = true;
                }else{
                    if(theTime > 60) {
                        theTime1 = parseInt(theTime/60);
                        theTime = parseInt(theTime%60);
                        if(theTime1 > 60) {
                            theTime2 = parseInt(theTime1/60);
                            theTime1 = parseInt(theTime1%60);
                            if(theTime2 > 24){
                                //大于24小时
                                theTime2 = parseInt(theTime2%24);
                            }
                        }
                    }
                    this.kaiInfo.hour = parseInt(theTime2)<10?'0'+theTime2:theTime2;
                    this.kaiInfo.minute = parseInt(theTime1)<10?'0'+theTime1:theTime1;
                    this.kaiInfo.second = parseInt(theTime)<10?'0'+theTime:theTime;
                }

            },
        }
    }
</script>


